import { Component, Input, OnInit } from '@angular/core';
import { PopoverController } from '@ionic/angular';
import { CustomerService } from '../customer.service';

import { Customer } from '../interface/customerInferface';
import { PopoverComponent } from '../popover/popover.component';
import { SmileyHuntingComponent } from "../smileyhunting/smileyhunting.component";

@Component({
  selector: 'app-listRegistrations',
  templateUrl: './listRegistrations.component.html',
  styleUrls: ['./listRegistrations.component.css']
})
export class ListRegistrationsComponent implements OnInit {

 customers: Customer[];
 searchTerm: string;

  constructor(
    private customersService: CustomerService,
    private smileyhunting: SmileyHuntingComponent,
    private popoverController: PopoverController
  ) { }

  async presentPopover(id: number) {
    const popover = await this.popoverController.create({
      component: PopoverComponent,
      componentProps: {id: id},
      translucent: true
    });
    return await popover.present(
    );
  }

  async DismissClick() {
    await this.popoverController.dismiss();
    this.reload();
      }

  ngOnInit() {
    this.customers = this.customersService.getAllCustomers();
  }

  backToHome(): void{
    console.log("zurück zum Menü");
  }

  reload() {
    window.location.reload();
  }

  startsmileyhunting(userid: number){
    console.log(userid)
    this.customersService.userIdHuntingService = userid; 
  }
}

