import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { IonicModule } from '@ionic/angular';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { RouterModule } from '@angular/router';

import { HomePage } from '../home/home.page';
import { ListRegistrationsComponent } from './listRegistrations.component';
import { Ng2SearchPipeModule } from 'ng2-search-filter';
import { SmileyHuntingModule } from '../smileyhunting/smileyhunting.module';
import { SmileyHuntingComponent } from "../smileyhunting/smileyhunting.component";


@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    IonicModule,
    ReactiveFormsModule,
    Ng2SearchPipeModule,
    RouterModule.forChild([
      {
        path: '',
        component: ListRegistrationsComponent
      },
      {
        path: 'home',
        component: HomePage
      },
    ]),
    
  ],
  declarations: [ListRegistrationsComponent],
  providers: [ListRegistrationsComponent, SmileyHuntingComponent],
})

export class ListRegistrationsModule {}